.project{
    margin: 50px 0px;
    margin-left: 150px;
}
.project hr{
    color: #138781;
    width: 400px;
    margin: 10px auto;
}
.project h2{
    font-weight: bold;
      color: #138781;
}
#ads{
    margin: 30px 0px 30px 0px;
}
#ads .card-notify-badge{
    position: absolute;
    left:-1px;
    top:-24px;
    background: orange;
    text-align: center;
    border-radius: 30px 30px 30px 30px;
    color: black;
    padding: 5px 10px;
    font-size: 12px;
}

.project .card img{
    height: 150px;
    width: 100%;
}
#ads .card-image-overlay{
    text-align: center;
    font-weight: bold;
    display: inline-block;
    margin-left: 5px;
    color: aliceblue;

}
.card-detail-badge {
    background-color: darkblue;
    text-align: center;
    border-radius: 30px 30px 30px 30px;
    font-size: 14px;
    padding: 5px 10px;
}
.project .card:hover{
    background-color: white;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    transition: all 1s ease;
}

.ad-btn{
    text-transform: uppercase;
    width: 150px;
    height: 40px;
    display: block;
    background-color: #138781;
    color: white;
    border-radius: 80px;
    font-size: 16px;
    line-height: 35px;
    text-decoration: none;
    margin: 20px auto 20px auto;
    overflow: hidden;
    position: relative;
    text-align: center;
}
.ad-btn:hover{
    background-color: white;
    color: #138781;
    background: transparent;
    border: 2px solid #138781;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    transition: all 0.5s ease;
}

/*=======================*/
@media(max-width: 600px){
    .project{
        margin: 0;
        padding: 0;
        margin-top: 50px;
    }
    .project hr{
        width: 280px;
    }
    .project .card{
        margin-top: 30px;
    }
}